'use client';

import React, { useState, useEffect } from 'react';
import { supabase } from '@/lib/supabase';
import { 
  Cpu, ShieldCheck, Key, Save, Loader2, 
  Sparkles, FileText, Printer, Zap, AlertCircle,
  BookOpen, BarChart3, Binary, Scale, Landmark
} from 'lucide-react';

export default function AIVaultPage() {
  const [loading, setLoading] = useState(true);
  const [saving, setSaving] = useState(false);
  const [isCalculating, setIsCalculating] = useState(false);
  const [prompt, setPrompt] = useState('');
  const [simulationResult, setSimulationResult] = useState<string | null>(null);
  
  // 5 PLATFORM LENGKAP
  const [keys, setKeys] = useState({
    openai_key: '',
    anthropic_key: '',
    google_key: '',
    mistral_key: '',
    llama_key: ''
  });

  useEffect(() => {
    const fetchProfile = async () => {
      const { data: { user } } = await supabase.auth.getUser();
      if (user) {
        const { data } = await supabase
          .from('profiles')
          .select('*')
          .eq('id', user.id)
          .maybeSingle();
        
        if (data) {
          setKeys({
            openai_key: data.openai_key || '',
            anthropic_key: data.anthropic_key || '',
            google_key: data.google_key || '',
            mistral_key: data.mistral_key || '',
            llama_key: data.llama_key || ''
          });
        }
      }
      setLoading(false);
    };
    fetchProfile();
  }, []);

  const handleSaveKeys = async () => {
    setSaving(true);
    const { data: { user } } = await supabase.auth.getUser();
    if (user) {
      const { error } = await supabase
        .from('profiles')
        .update({
          openai_key: keys.openai_key,
          anthropic_key: keys.anthropic_key,
          google_key: keys.google_key,
          mistral_key: keys.mistral_key,
          llama_key: keys.llama_key
        })
        .eq('id', user.id);
      
      if (!error) {
        alert('Intelligence Vault Updated Successfully');
      }
    }
    setSaving(false);
  };

  const handleRunSimulation = async () => {
    if (!prompt) return alert('Please enter a tax scenario');
    if (!keys.google_key && !keys.openai_key) return alert('Please provide an API Key first');

    setIsCalculating(true);
    setSimulationResult(null);

    try {
      const response = await fetch(`https://generativelanguage.googleapis.com/v1beta/models/gemini-pro:generateContent?key=${keys.google_key}`, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
          contents: [{ parts: [{ text: `Analyze this international tax scenario and provide the Withholding Tax (WHT) rate based on global treaties: ${prompt}` }] }]
        })
      });

      const data = await response.json();
      const text = data.candidates[0].content.parts[0].text;
      setSimulationResult(text);
    } catch (error) {
      setSimulationResult("Error: Connection failed. Check your API Key and quota.");
    } finally {
      setIsCalculating(false);
    }
  };

  if (loading) return (
    <div className="min-h-screen flex items-center justify-center bg-[#FDFCFB]">
      <Loader2 className="animate-spin text-[#D4AF37]" size={40} />
    </div>
  );

  const hasKeys = Object.values(keys).some(k => k.trim() !== '');

  return (
    <div className="min-h-screen bg-[#FDFCFB] text-black pb-32 font-sans">
      
      {/* 1. HERO SECTION */}
      <section className="max-w-6xl mx-auto pt-24 pb-12 px-8 text-center space-y-6">
        <div className="inline-flex items-center gap-3 px-6 py-2 rounded-full bg-black text-[#D4AF37] text-[12px] font-black uppercase tracking-[0.4em]">
          <ShieldCheck className="w-4 h-4" /> Secure Intelligence Hub
        </div>
        <h1 className="text-6xl font-serif italic leading-tight text-black">
          AI Tax <span className="text-[#D4AF37]">Vault.</span>
        </h1>
        <p className="max-w-3xl mx-auto text-gray-400 font-light italic text-[18px] leading-relaxed">
          The Vault is your private engine room. Integrate your own API keys to gain direct access to autonomous fiscal reasoning.
        </p>
      </section>

      {/* 2. API KEY MANAGEMENT - 5 PLATFORMS */}
      <section className="max-w-6xl mx-auto px-8 mb-20">
        <div className="bg-white border border-gray-100 rounded-[4rem] p-12 shadow-2xl shadow-gray-100/50">
          <div className="flex items-center gap-4 mb-12 border-b border-gray-50 pb-8">
            <Key className="text-[#D4AF37]" size={28} />
            <h2 className="text-[14px] font-black uppercase tracking-[0.4em]">Initialize Your Engine</h2>
          </div>

          <div className="grid md:grid-cols-3 gap-8 mb-8">
            <div className="p-8 bg-[#FDFCFB] border border-gray-100 rounded-[2.5rem] space-y-4">
              <p className="font-bold text-black uppercase tracking-widest text-[12px]">OpenAI</p>
              <input type="password" value={keys.openai_key} onChange={(e) => setKeys({...keys, openai_key: e.target.value})} placeholder="sk-proj-..." className="w-full bg-white border border-gray-100 rounded-xl py-3 px-4 text-[13px] focus:outline-none focus:border-[#D4AF37]" />
            </div>
            <div className="p-8 bg-[#FDFCFB] border border-gray-100 rounded-[2.5rem] space-y-4">
              <p className="font-bold text-black uppercase tracking-widest text-[12px]">Anthropic</p>
              <input type="password" value={keys.anthropic_key} onChange={(e) => setKeys({...keys, anthropic_key: e.target.value})} placeholder="xkeys-..." className="w-full bg-white border border-gray-100 rounded-xl py-3 px-4 text-[13px] focus:outline-none focus:border-[#D4AF37]" />
            </div>
            <div className="p-8 bg-[#FDFCFB] border border-gray-100 rounded-[2.5rem] space-y-4">
              <p className="font-bold text-black uppercase tracking-widest text-[12px]">Google Gemini</p>
              <input type="password" value={keys.google_key} onChange={(e) => setKeys({...keys, google_key: e.target.value})} placeholder="AIzaSy..." className="w-full bg-white border border-gray-100 rounded-xl py-3 px-4 text-[13px] focus:outline-none focus:border-[#D4AF37]" />
            </div>
          </div>
          <div className="grid md:grid-cols-2 gap-8">
             <div className="p-8 bg-[#FDFCFB] border border-gray-100 rounded-[2.5rem] space-y-4">
              <p className="font-bold text-black uppercase tracking-widest text-[12px]">Mistral AI</p>
              <input type="password" value={keys.mistral_key} onChange={(e) => setKeys({...keys, mistral_key: e.target.value})} placeholder="Mistral API Key..." className="w-full bg-white border border-gray-100 rounded-xl py-3 px-4 text-[13px] focus:outline-none focus:border-[#D4AF37]" />
            </div>
            <div className="p-8 bg-[#FDFCFB] border border-gray-100 rounded-[2.5rem] space-y-4">
              <p className="font-bold text-black uppercase tracking-widest text-[12px]">Meta Llama</p>
              <input type="password" value={keys.llama_key} onChange={(e) => setKeys({...keys, llama_key: e.target.value})} placeholder="Llama API Key..." className="w-full bg-white border border-gray-100 rounded-xl py-3 px-4 text-[13px] focus:outline-none focus:border-[#D4AF37]" />
            </div>
          </div>
          <div className="mt-12 flex justify-center">
            <button onClick={handleSaveKeys} disabled={saving} className="px-12 py-5 bg-black text-[#D4AF37] rounded-2xl text-[13px] font-black uppercase tracking-[0.2em] hover:bg-[#D4AF37] hover:text-black transition-all shadow-xl">
              {saving ? <Loader2 className="animate-spin" /> : <><Save size={18} className="inline mr-2" /> Sync Intelligence Vault</>}
            </button>
          </div>
        </div>
      </section>

      {/* 3. DYNAMIC INTERFACE: INFO VS CALCULATOR */}
      <section className="max-w-6xl mx-auto px-8">
        <div className={`p-16 rounded-[4rem] border-2 border-dashed ${hasKeys ? 'bg-white border-gray-100' : 'bg-gray-50 border-gray-200'}`}>
          {!hasKeys ? (
             <div className="space-y-16 animate-in fade-in duration-700">
               {/* Informasi Manfaat Saat Key Masih Kosong */}
               <div className="text-center space-y-4">
                 <AlertCircle className="mx-auto text-gray-300" size={48} />
                 <h3 className="text-2xl font-serif italic text-gray-400">Calculator Awaiting Activation</h3>
                 <p className="text-gray-400 italic font-light">Integrating your API keys unlocks the following professional capabilities:</p>
               </div>
               
               {/* Fitur yang Akan Terbuka (Usage & Function Info) */}
               <div className="grid md:grid-cols-4 gap-8">
                  <div className="p-8 bg-white/50 border border-gray-100 rounded-3xl space-y-4 text-center">
                    <FileText className="mx-auto text-gray-300" size={24} />
                    <p className="text-[10px] font-black uppercase tracking-widest text-gray-400">Treaty Dissection</p>
                  </div>
                  <div className="p-8 bg-white/50 border border-gray-100 rounded-3xl space-y-4 text-center">
                    <Scale className="mx-auto text-gray-300" size={24} />
                    <p className="text-[10px] font-black uppercase tracking-widest text-gray-400">Legal Benchmarking</p>
                  </div>
                  <div className="p-8 bg-white/50 border border-gray-100 rounded-3xl space-y-4 text-center">
                    <Printer className="mx-auto text-gray-300" size={24} />
                    <p className="text-[10px] font-black uppercase tracking-widest text-gray-400">Audit Reports</p>
                  </div>
                  <div className="p-8 bg-white/50 border border-gray-100 rounded-3xl space-y-4 text-center">
                    <Zap className="mx-auto text-gray-300" size={24} />
                    <p className="text-[10px] font-black uppercase tracking-widest text-gray-400">Instant Modeling</p>
                  </div>
               </div>
             </div>
          ) : (
            <div className="space-y-12 animate-in fade-in zoom-in duration-500">
               <div className="flex items-center gap-4 text-[#D4AF37]"><Sparkles size={28} /><h2 className="text-2xl font-serif italic text-black">Precision Fiscal Calculator</h2></div>
               <div className="grid md:grid-cols-2 gap-12">
                  <div className="space-y-6 text-left">
                    <textarea value={prompt} onChange={(e) => setPrompt(e.target.value)} placeholder="Input scenario here..." className="w-full h-48 bg-[#FDFCFB] border border-gray-100 rounded-[2rem] p-8 text-[15px] focus:outline-none focus:border-[#D4AF37] resize-none" />
                    <button onClick={handleRunSimulation} disabled={isCalculating} className="w-full py-5 bg-black text-[#D4AF37] rounded-2xl text-[13px] font-black uppercase tracking-[0.2em] flex items-center justify-center gap-3">
                      {isCalculating ? <Loader2 className="animate-spin" /> : <><Cpu size={18} /> Run Intelligence Simulation</>}
                    </button>
                  </div>
                  <div className="bg-black rounded-[3rem] p-12 text-white flex flex-col justify-center items-center text-center space-y-8 shadow-2xl overflow-y-auto max-h-[500px]">
                     {simulationResult ? (
                        <div className="w-full space-y-4 animate-in fade-in">
                           <h3 className="text-[#D4AF37] font-serif italic border-b border-white/10 pb-4">Analysis Result</h3>
                           <p className="text-gray-300 text-[14px] leading-relaxed text-left whitespace-pre-wrap">{simulationResult}</p>
                        </div>
                     ) : (
                        <div className="opacity-40 space-y-4">
                           <Printer className="mx-auto text-[#D4AF37]" size={40} />
                           <p className="text-gray-400 font-light text-[14px] italic leading-relaxed">Once calculated, your full breakdown and PDF report will appear here.</p>
                        </div>
                     )}
                  </div>
               </div>
            </div>
          )}
        </div>
      </section>
    </div>
  );
}